@charset "UTF-8";
/* https://cn.vuejs.org/v2/api/#v-cloak */
[v-cloak] {
  display: none !important;
}
.el-button {
  border-radius: 0 !important;
}
.el-input.is-disabled .el-input__inner {
  cursor: default;
}
.el-select .el-input.is-disabled .el-input__inner {
  cursor: default;
}
.footer .links {
  text-align: center;
  color: #999;
  line-height: 30px;
}
.footer .links .link {
  padding: 0 10px;
}
.footer .copyright {
  text-align: center;
  color: #999;
  line-height: 30px;
}
/* 通用头部导航 */
.header .logo {
  position: absolute;
  left: 60px;
  top: 34px;
  display: block;
  font-size: 36px;
  color: #409eff;
  font-weight: bold;
  text-decoration: none;
}
/* 面包屑导航 */
.crumb {
  margin-bottom: 15px;
  height: 50px;
  line-height: 50px;
  background: #eee;
  color: #888;
  font-size: 14px;
  border-top: 2px solid #409eff;
}
.nav {
  background: #eee;
  height: 30px;
  line-height: 30px;
}
/* 用户部分 */
.nav .user {
  float: left;
}
.nav .user.login {
  display: block;
}
.nav .user.not-login {
  display: block;
}
.nav .user .link {
  margin-right: 5px;
}
/* 导航链接部分 */
.nav .nav-list {
  float: right;
}
.nav .nav-list .nav-item {
  display: inline-block;
}
/* 导航主体 */
.nav-side {
  float: left;
  width: 130px;
  min-height: 100px;
}
.nav-side .nav-item {
  line-height: 25px;
  font-size: 13px;
}
.nav-side .nav-item .link {
  color: #888;
}
.nav-side .nav-item.active .link {
  color: #337ab7;
}
/* 右侧内容区 */
.content.with-nav {
  float: left;
  width: 950px;
}
.nav-simple {
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #ddd;
  background: #fff;
}
.nav-simple .logo {
  font-size: 26px;
  font-weight: bold;
  color: #337ab7;
  text-decoration: none;
}
/* css reset */
* {
  margin: 0;
  padding: 0;
}
body {
  background: #ffffff;
  font-size: 12px;
  font-weight: normal;
  font-stretch: normal;
}
li {
  list-style: none;
}
img {
  display: block;
  border: none;
}
label {
  cursor: pointer;
}
input[type='checkbox'] {
  cursor: pointer;
}
/* 定宽布局 */
.w {
  width: 1080px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}
/* panel */
.panel {
  padding: 10px;
  margin-bottom: 10px;
  background: #fff;
}
.panel-color {
  background: #fff;
}
.panel .panel-title {
  padding: 10px;
  font-size: 14px;
  color: #666;
  font-weight: bold;
  border-bottom: 1px solid #eee;
}
.panel .panel-body {
  padding: 10px;
  overflow: hidden;
}
.panel-padding {
  padding: 10px 0 !important;
}
/* 全局通用样式 */
/* 隐藏类 */
.hide {
  display: none;
}
/* 超链样式 */
.link {
  color: #999;
  cursor: pointer;
  text-decoration: none;
}
.link:hover {
  color: #e80012;
}
.link-text {
  color: #999;
  text-decoration: none;
}
/* btn */
.btn {
  display: inline-block;
  padding: 0 20px;
  height: 40px;
  line-height: 40px;
  vertical-align: middle;
  border: none;
  background: #409eff;
  font-size: 17px;
  font-weight: bold;
  color: #fff;
  outline: none;
  cursor: pointer;
  text-decoration: none;
}
.btn-mini {
  height: 25px;
  line-height: 25px;
  font-size: 12px;
  padding: 0 10px;
}
/* loading */
.loading {
  margin: 10px auto;
  display: block;
  width: 65px;
  height: 65px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background: url(/static/img/loading.2cfec07.gif) no-repeat;
  opacity: .6;
}
a, body, button, h1, h2, h3, h4, html, input, li {
  font-family: "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", Arial, sans-serif !important;
}
.cart-table {
  width: 100%;
  border-collapse: collapse;
  border: 1px solid #ebebeb;
  margin-bottom: 10px;
}
/* header */
.cart-header {
  background: #eee;
}
.cart-header .cart-cell {
  height: 40px;
  line-height: 40px;
}
.cart-table .cell-check {
  width: 6%;
  text-align: left;
  padding-left: 20px;
}
.cart-table .cell-img {
  width: 10%;
  text-align: left;
}
.cart-table .cell-img .p-img {
  margin: 10px 0;
  width: 80px;
  height: 80px;
  border: 1px solid #ddd;
}
.cart-table .cell-info {
  width: 35%;
  text-align: left;
}
.cart-header .cell-info {
  width: 45%;
  padding-left: 106px;
}
.cart-table .cell-price {
  width: 10%;
  text-align: center;
}
.cart-table .cell-count {
  width: 15%;
  text-align: center;
}
.cart-table .cell-count .count-input {
  width: 60px;
  height: 28px;
  line-height: 28px;
  border: 1px solid #ddd;
  text-align: center;
  vertical-align: middle;
  outline: none;
  color: #333;
}
.cart-table .cell-count .count-btn {
  display: inline-block;
  width: 20px;
  height: 28px;
  line-height: 28px;
  border: 1px solid #ddd;
  vertical-align: middle;
  cursor: pointer;
  background: #fff;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.cart-table .cell-total {
  width: 14%;
  text-align: center;
  font-weight: bold;
}
.cart-table .cell-opera {
  width: 10%;
  text-align: center;
}
/* footer */
.cart-footer {
  position: relative;
  line-height: 50px;
  background: #eee;
  overflow: hidden;
}
.cart-footer .select-con {
  float: left;
  padding-left: 20px;
}
.cart-footer .delete-con {
  float: left;
  margin-left: 20px;
}
.cart-footer .submit-con {
  float: right;
}
.cart-footer .submit-con .submit-total {
  font-size: 18px;
  color: #337ab7;
  font-weight: bold;
  margin-right: 30px;
  vertical-align: middle;
}
.cart-footer .submit-con .btn-submit {
  width: 80px;
  text-align: center;
  height: 50px;
  line-height: 50px;
}
/* 错误信息 */
.err-tip {
  height: 60px;
  line-height: 60px;
  color: #f60;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  background: #eee;
}
/* 图片区 */
.intro-wrap {
  overflow: hidden;
}
.intro-wrap .p-img-con {
  float: left;
  width: 500px;
}
/* 主图 */
.intro-wrap .p-img-con .main-img-con {
  /* height: 400px;
    border: 1px solid #ddd; */
}
.intro-wrap .p-img-con .main-img-con .main-img {
  height: 100%;
  width: 100%;
}
/* 缩略图 */
.intro-wrap .p-img-con .p-img-list {
  overflow: hidden;
  height: 76px;
  margin-top: 5px;
  margin-right: -10px;
}
.intro-wrap .p-img-con .p-img-item {
  float: left;
  margin-right: 10px;
  width: 72px;
  height: 72px;
  border: 2px solid #eee;
  cursor: pointer;
}
.intro-wrap .p-img-con .p-img-item:hover {
  border: 2px solid #337ab7;
}
.intro-wrap .p-img-con .p-img-item .p-img {
  width: 100%;
  height: 100%;
}
/* 商品信息 */
.intro-wrap .p-info-con {
  float: left;
  width: 550px;
  margin-left: 30px;
}
.intro-wrap .p-info-con .p-name {
  font-size: 20px;
  color: #333;
}
.intro-wrap .p-info-con .p-subtitle {
  font-size: 15px;
  color: #666;
  line-height: 30px;
}
.intro-wrap .p-info-con .p-subtitle-2 {
  font-size: 12px;
  color: #666;
  line-height: 30px;
}
.intro-wrap .p-info-con .p-subtitle-3 {
  font-size: 12px;
  color: #4d7fff;
  line-height: 30px;
}
.intro-wrap .p-info-con .p-info-item {
  position: relative;
  /* padding: 0 10px; */
  margin-top: 10px;
}
.intro-wrap .p-info-con .p-info-item-2 {
  position: relative;
  /* padding: 0 10px; */
  margin-top: 10px;
}
.intro-wrap .p-info-con .p-info-item .label {
  display: inline-block;
  /* width: 50px; */
  font-size: 36px;
}
/* 价格部分 */
.intro-wrap .p-info-con .p-price-con {
  padding: 7px;
  /* background-color: #4d7fff; */
  background: url(http://oss.lshao.cn/images/price_bg.jpg) no-repeat;
}
.intro-wrap .p-info-con .p-price-con .info {
  color: #337ab7;
  font-size: 18px;
}
/* 数量部分 */
.intro-wrap .p-info-con .p-count-con .p-count {
  height: 36px;
  line-height: 36px;
  font-size: 15px;
  width: 45px;
  text-align: center;
  outline: none;
  border: 1px solid #aaa;
}
.intro-wrap .p-info-con .p-count-con .p-count-btn {
  position: absolute;
  left: 115px;
  display: block;
  width: 20px;
  height: 15px;
  line-height: 15px;
  text-align: center;
  font-size: 10px;
  border: 1px solid #aaa;
  background: #fff;
  cursor: pointer;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.intro-wrap .p-info-con .p-count-con .p-count-btn.plus {
  top: 0;
}
.intro-wrap .p-info-con .p-count-con .p-count-btn.minus {
  bottom: 0;
}
/* 商品详细信息 */
.detail-wrap {
  margin-top: 15px;
}
.detail-wrap .tab-list {
  background: #eee;
  border: 1px solid #ddd;
  overflow: hidden;
  border-bottom: 1px solid #337ab7;
}
.detail-wrap .tab-list .tab-item {
  float: left;
  width: 270px;
  height: 50px;
  line-height: 50px;
  font-size: 16px;
  text-align: center;
  cursor: pointer;
}
.detail-wrap .tab-list .tab-item:last-child {
  width: 268px;
}
.detail-wrap .tab-list .tab-item.active {
  background: #337ab7;
  color: #fff;
}
.detail-wrap .detail-con {
  padding-top: 20px;
  background: #fff;
}
.detail-wrap .detail-con img {
  display: block;
  margin: 0 auto;
}
/* 错误信息 */
.err-tip {
  height: 60px;
  line-height: 60px;
  color: #f60;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  background: #eee;
}
/* keywords-list */
.keywords-list {
  float: left;
  width: 240px;
  height: 370px;
  padding: 10px 0;
  margin-right: 10px;
  background: #666;
  color: #fff;
}
.keywords-list .keywords-item {
  padding-left: 10px;
  height: 35px;
  line-height: 35px;
  font-size: 14px;
}
.keywords-list .keywords-item:first-child {
  border-bottom: solid 1px #409eff;
}
.keywords-list .keywords-item:hover {
  background: #999;
}
.keywords-list .keywords-item .link {
  margin-right: 63px;
  color: #fff;
}
.keywords-list .keywords-item .link:hover {
  color: #337ab7;
}
/* banner-con */
.banner-con {
  width: 830px;
  float: left;
